.container{
    max-width: 1200px;
    margin: 0 auto;
}

header.banner {
    height: 100vh;
    background-image: url(../images/banner.png);
    background-repeat: no-repeat;
    background-size: cover;
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
}

header .banner-images {
    display: flex;
    justify-content: space-between;
}

header .banner-images a:hover{
    opacity: 0.8;
}

footer.footer {
    background: #000;
    color: #ffffff;
    padding: 64px 0px;
    text-align: center;
}

.about {
    text-align: center;
    padding: 56px 0px;
}

.about .work {
    border-top: 1px solid #000;
    width: 384px;
    margin: 0 auto;
}

.d-flex {
    display: flex;
    justify-content: center;
    align-items: center;
}

.dot {
    height: 22px;
    width: 22px;
    background-color: #891818;
    border-radius: 50%;
    display: inline-block;
}

.brand {
    background: #000;
    color: #ffffff;
    text-align: center;
    padding: 56px 0px 160px 0px;
}

.brand-img {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.other {
    text-align: center;
    padding: 56px 0px 144px 0px;
}

.other .other-img {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}